import HeaderBack from '../Common/HeaderBack'

export default {
  components: {
    myHeader: HeaderBack,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back",
        HBC_text:"会员中心",
        HBR_icon: " ",
      },
      infoHeader: require('../../assets/images/mine/self001.jpg'),
      isUp: 'upIcon',
      isDown: 'downIcon',
      isUpCont: 'strategy-select',
      isDownCont: 'strategy-content',
      isA: false,
      isB: false,
      isC: false,
      isD: false,
    }
  },
  methods: {
    toggle: function(index){
      if(index==1){
        this.isA = !this.isA;
      }else if(index==2){
        this.isB = !this.isB;
      }else if(index==3){
        this.isC = !this.isC;
      }else if(index==4){
        this.isD = !this.isD;
      }   				
    },
  },
  created() {}
}



